import {Config} from '../models';

/**
 * Created by Atlantismonk on 2017/2/25.
 */

class AdminController{
    async showConfiguration(ctx,next){
        //todo 把fetch()换成findOne()
        await Config.fetch(function(err,configs) {
            if(err) ctx.throw(err);
            ctx.body = configs;
        });
    }

    
}